<template>
  <div class="sz-main packageBillSummary">
    <show-more v-model="ishowHeights">
      <div class="sz-search" slot="collapse">
        <div class="dh_content" style="display:inline-block;width:calc(100% - 73px);padding-top:16px;">
          <ul>
            <li>
              <el-select v-model="type" class="input-80">
                <el-option :key="1" label="账单日期" :value="1"></el-option>
                <el-option :key="2" label="扣款时间" :value="2"></el-option>
              </el-select>
              <sz-date-picker
                v-if="type == 1"
                v-model="dateValue"
                type="daterange"
                value-format="yyyy-MM-dd"
                range-separator="至"
                :limit="limit"
                :clearable="false"
                :editable="false"
                :isCheckMsg="false"
                class="input-160"
              />
              <sz-date-picker
                v-if="type != 1"
                v-model="timeValue"
                type="datetimerange"
                valueFormat="yyyy-MM-dd HH:mm:ss"
                range-separator="至"
                start-placeholder="开始时间"
                end-placeholder="结束时间"
                :limit="limit"
                :clearable="false"
                :editable="false"
                :isCheckMsg="false"
                class="input-160"
              />
            </li>
            <li>
              <label>固定费类型：</label>
              <sz-accountFee v-model="accountFee" class="inputh-85"/>
            </li>
            <li>
                <label>扣款状态：</label>
                <el-select v-model="status" class="inputh-85">
                  <el-option
                    v-for=" item in statusOptions" 
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  >
                  </el-option>
                </el-select>
              </li>
          </ul>
        </div>
        <div style="display:inline-block;">
            <el-button type="primary" @click="onSearch" icon="el-icon-search">查询</el-button>
        </div>
      </div>
    </show-more>
    <div class="sz-search-btn" style="border-bottom:0px;">
      <div class="ulH">
  
      </div>
      <div>
        <el-button type="primary" @click="searchExport" icon="el-icon-download">导出</el-button>
      </div>
    </div>
    <div class="sz-content" :style="{height:contentHeight+ishowHeight+'px'}">
      <spread :ngData="tableData" :headData="tableHead" @checkboxAllClick="checkboxAllClick"/>
      <div class="sz-pagination-box">
        <sz-pagination 
          :page="pagination.currentPage"
          :pageSize="pagination.pageSize"
          :total="pagination.total"
          @fetchData="fetchData"
        />
      </div>
    </div>
    <csv
      :visible="exportObj.visible"
      :fileName="exportObj.fileName"
      :head="tableHead"
      :paramData="exportObj.excelData"
      :sum="exportObj.sum"
      :current="exportObj.current"
      @clear="closeDialog"
    />
    <sz-batch-process
      :title="batchTitle"
      :dialogVisible="batchDialogVisible"
      :columns="columns"
      :tableData="cloneCrrow"
      :batchUrl="batchUrl"
      @model="close"
    />    
  </div>
</template>
<script>
import { pagerMixin } from "@/utils/mixin";
import { cloneDeep, trim } from "lodash";
import { checkPackageBillS, checkPackageBillSUrl} from "@/config/api";
import { billsTypeCodesNow, deductionModel  } from "@/utils/codeType";
import { billUtils } from "@zto_module/shenzhou-components-static";
export default {
  mixins: [pagerMixin],
  data(){
    return{
      accountFee:"",
      ishowHeights:0,
      ishowHeight:0,
      currRow: [],
      type:1,
      limit:31,
      dateValue: [
        this.moment().subtract(1, "days").format("YYYY-MM-DD"),
        this.moment().subtract(1, "days").format("YYYY-MM-DD")
      ],
      timeValue:[
        this.moment().subtract(1, "days").format("YYYY-MM-DD") + " 00:00:00",
        this.moment().subtract(1, "days").format("YYYY-MM-DD") + " 23:59:59"
      ],
      billsTypeStatus:-2,
      billsTypeStatusOptions:[
        { value: -2, label: "全部" },
        { value: 0, label: "客户" },
        { value: 4, label: "业务员" },
        { value: 2, label: "承包区" },
        { value: 99, label: "下级网点" },
      ],
      accountUserId:"",
      status: -1,
      statusOptions: [
        { value: -1, label: "全部" },
        { value: 0, label: "未扣款" },
        { value: 1, label: "待扣款" },
        { value: 2, label: "已线上扣款" },
        { value: 3, label: "已线下收款" },
      ],
      tableData:[],
      tableHead: {
        columns: [
          { 
            displayName: "扣款状态", 
            name: "status", 
            size: 100,
            value: row => {
              if(row.status === 0){
                return "未扣款";
              }else if(row.status === 1 || row.status === 5){
                return "待扣款";
              }else if(row.status === 2){
                return "已线上扣款";
              }else if(row.status === 3){
                return "已线下收款";
              }else{
                return "";
              }
            }
          },
          { displayName: "账单日期", name: "settlementTime", size: 120, formatter: "yyyy-MM-dd" },
          { displayName: "固定费类型", name: "fixedTypeName", size: 100, isCode: true },
          { displayName: "结算对象", name: "objectName", size: 100 , isCode: true },
          { displayName: "结算对象编码", name: "objectCode", size: 100 , isCode: true},
          { 
            displayName: "扣款模式", 
            name: "mode", 
            size: 150,
            value: row => deductionModel(Number(row.mode))
          },
          {
            displayName: "金额",
            name: "fixedFee",
            size: 100,
            showTotal: true,
            formatter: "0.00"
          },
          { displayName: "扣款人", name: "hexiaoMan", size: 100 , isCode: true},
          {
            displayName: "扣款时间",
            name: "heixaoTime",
            size: 150,
            formatter: "yyyy-MM-dd hh:mm:ss"
          },
          { displayName: "创建人", name: "creator", size: 100 , isCode: true},
          { displayName: "备注", name: "remark", size: 400 ,isConversionComma:true}
        ]
      },
      exportObj: {
        fileName: "对包仓和固定费"
      },
      optionsClone:"",
      requestUrl:"/quote/new/lowerWaterBillAuto/checkQuery",
      requestMode:'get',
      batchDialogVisible:false,
      columns:[
        { displayName: '账单日期', name: 'settlementTime'},
        { displayName: '结算对象', name: 'objectName'},
        { displayName: '金额', name: 'fixedFee'}
      ],
      batchTitle:"",
      batchUrl:"",
      dialogVisibleEdit:false,
      billDate:this.moment().format("YYYY-MM-DD"),
      pickerOptions: {
          disabledDate(time) {
              return time.getTime() > Date.now();
          }
      },
      accountUserIdEdit:"",
      accountFeeEdit:"",
      deduction:4,
      deductionOptions:[
        { value: 4, label: "自动扣款" },
        { value: 3, label: "手动扣款" },
      ],
      feeEdit:0,
      remarkText:"",
      objectType:-1,
      objectTypeEdit:-1,
      cloneCrrow:[]
    }
  },
  methods: {
    changtype(val){
      if(val){
        this.limit = 31;
      }else{
        this.limit = 1;
      }
      if(val){
        this.objectType =  val.type;
      }else{
        this.objectType = "";
      }
    },
    changtypeEdit(val){
      if(val){
        this.objectTypeEdit = val.type;
      }else{
        this.objectTypeEdit = "";
      }
    },
    handleClose(){
      this.dialogVisibleEdit = false;
    },
    close(){
      this.batchDialogVisible = false;
      this.onSearch();
    },
    checkboxAllClick(val) {
      this.selectionSite([], val);
    },
    selectionSite(rows, row) {
      this.currRow = this.tableData.filter(res => {
        return res.check == true;
      });
    },
    checkBill(self){
      this.batchTitle = self.toElement.innerText;
      if(this.batchTitle != "手工入账单"){
        if(this.currRow.length === 0){
          this.msg('请选择需要结算的数据！');
          return
        }
      }
      if(this.batchTitle == "手工入账单"){
        this.billDate = this.moment().format("YYYY-MM-DD");
        this.accountUserIdEdit = "";
        this.accountFeeEdit = "";
        this.deduction = 4;
        this.feeEdit = 0;
        this.remarkText = "";
        this.dialogVisibleEdit = true;
      }else{
        this.checkBillPost();
      }
    },
    checkBillPost(){
      this.cloneCrrow = cloneDeep(this.currRow); 
      if(this.batchTitle == "手工入账单"){
        if(!this.accountUserIdEdit){
          this.msg("请选择结算对象");
          return;
        }
        if(!this.accountFeeEdit){
          this.msg("请选择固定费类型");
          return;
        }
        if(this.feeEdit === 0){
          this.msg("费用金额要大于0");
          return;
        }
        let optionh = {
          settlementTime: this.moment(this.billDate).format("YYYY-MM-DD"),
          objectId: this.accountUserIdEdit,
          objectType:this.objectTypeEdit,
          fixedType: this.accountFeeEdit,
          mode: this.deduction,
          fixedFee: this.feeEdit,
          remark: this.remarkText
        };
      }else if(this.batchTitle == "扣预付款" || this.batchTitle == "线下收款"){
        if(this.batchTitle == "线下收款"){
          this.cloneCrrow.forEach(item => {
            item.type = 1;
          });
        }
        this.cloneCrrow.forEach(res => {
          res.settlementTime = this.moment(res.settlementTime).format("YYYY-MM-DD");
        });
        this.cloneCrrow = cloneDeep(this.cloneCrrow);
        this.batchUrl = "/quote/new/lowerWaterBillAuto/hexiao";
        this.batchDialogVisible = true;
      }else if(this.batchTitle == "取消扣款"){
        this.cloneCrrow.forEach(res => {
          res.settlementTime = this.moment(res.settlementTime).format("YYYY-MM-DD");
        });
        this.cloneCrrow = cloneDeep(this.cloneCrrow);
        this.batchUrl = "/quote/new/lowerWaterBillAuto/fanhexiao";
        this.batchDialogVisible = true;
      }
    },
    onSearch(){
      this.currRow = [];
      if(this.type === 1){
        if (this.dateValue[2] < 0) {
          this.msg("开始日期不能大于结束日期,请重新选择日期");
          return;
        } else {
          if (this.limit <= this.dateValue[2]) {
            this.msg("最多可查询" + this.limit + "天的数据,请重新选择日期");
            return;
          }
        }
      }else{
        if (this.timeValue[2] < 0) {
          this.msg("开始时间不能大于结束时间,请重新选择时间");
          return;
        } else {
          if (this.limit <= this.timeValue[2]) {
            this.msg("最多可查询" + this.limit + "天的数据,请重新选择时间");
            return;
          }
        }
      }
      this.pagination.currentPage = 1;
      let options = {
        timeType:this.type,
        startTime: this.type === 1? (this.dateValue[0] == null ? "":this.dateValue[0]) : (this.timeValue[0] == null ? "":this.timeValue[0]),
        endTime: this.type === 1 ? (this.dateValue[1] == null ? "":this.dateValue[1]) : (this.timeValue[1] == null ? "":this.timeValue[1]),
        fixedType:this.accountFee,
        objectId:this.accountUserId,
        pageSize: this.pagination.pageSize,
        currentPage: this.pagination.currentPage
      };
      if(this.billsTypeStatus !== -2){
        options.objectType = this.billsTypeStatus;
      }
      if(this.status !== -1){
        options.deductStatus = this.status;
      }
      if(this.accountUserId){
        options.objectIdType = this.objectType;
      }
      this.optionsClone = cloneDeep(options);
      this.query(options);
    },
    query(options) {
      checkPackageBillS(options)
        .then(res => {
          if (res.data.list && res.data.list.length > 0) {
            this.tableData = res.data.list;
            this.pagination.total = res.data.totalRow;
          } else {
            this.pagination.total = 0;
            this.tableData = [];
            this.msg("没有数据哟");
          }
        }).catch(res => {});
    },
    proving(val) {
      val = val+"";
      val = val.replace(/[^\.\d\n]/g, "");
      val = val.replace(/[^\d.]/g,"");
      val = val.replace(/^\./g, "");
      val = val.replace(/\.{2,}/g,".");
      val = val.replace(".","$#$").replace(/\./g,"").replace("$#$","."); 
      val = val.replace(/^(\-)*(\d+)\.(\d\d).*$/,'$1$2.$3');//只能输入两个小数  
      if(val.indexOf(".") < 0 && val !=""){//以上已经过滤，此处控制的是如果没有小数点，首位不能为类似于 01、02的金额 
          val= parseFloat(val); 
      }
      return val;
    }
  },
  watch: {
    ishowHeights: {
      handler(val, oldVal) {
        if (val === 0) {
          this.ishowHeight = val;
          this.z_method.refreshSpread();
          return;
        }
        setTimeout(() => {
          this.ishowHeight = val;
          this.z_method.refreshSpread();
        }, 400);
      },
      deep: true
    },
    feeEdit:{
      handler(val, oldVal) {
        if(!val){
          this.feeEdit = 0;
          return;
        }
        let str = val+"";
        if(str.length <= 7){
          str = str.split('.');
          if(str[0].length <=4){
            this.feeEdit = this.proving(val);
          }else{
            this.feeEdit = oldVal;
          }
        }else{
          this.feeEdit = oldVal;
        }
      }
    },
    remarkText:{
      handler(val, oldVal) {
        if(billUtils.regRemark(val)){
          this.remarkText = val;
        }else{
          this.remarkText = oldVal;
        }
      }
    },
  },
  computed: {
    contentHeight() {
      if(this.z_param.isActiveH){
        return this.$store.getters.clientHeight - 140;
      }else{
        return this.$store.getters.clientHeight - 210;
      }
    }
  }
};
</script>
